'use client'

import { useEffect, useState } from 'react'
import { FileText, FileSpreadsheet, Plus } from 'lucide-react'
import CrudLayout from '../../components/CrudLayout'
import CrudTable from '../../components/CrudTable'
import CrudPagination from '../../components/CrudPagination'
import { api } from '@/lib/api'

type Usuario = {
  user_id: string
  nome: string
  email: string
  perfil: 'Administrador' | 'Usuario'
  ativo: 'Sim' | 'N√£o'
  foto?: string | null
}

export default function UsuariosPage() {
  const [data, setData] = useState<Usuario[]>([])
  const [busca, setBusca] = useState('')
  const [page, setPage] = useState(1)
  const [total, setTotal] = useState(0)

  const [modalOpen, setModalOpen] = useState(false)
  const [editing, setEditing] = useState<Usuario | null>(null)

  const [form, setForm] = useState({
    nome: '',
    email: '',
    perfil: 'Usuario',
    ativo: 'Sim',
    foto: null as File | null,
  })

  const pageSize = 10

  async function carregarUsuarios() {
    const res = await api.get(
      `/entity/usuarios?page=${page}&limit=${pageSize}&search=${busca}`,
    )
    setData(res.data)
    setTotal(res.total)
  }

  useEffect(() => {
    carregarUsuarios()
  }, [page, busca])

  function abrirEditar(usuario: Usuario) {
    setEditing(usuario)
    setForm({
      nome: usuario.nome,
      email: usuario.email,
      perfil: usuario.perfil,
      ativo: usuario.ativo,
      foto: null,
    })
    setModalOpen(true)
  }

  async function salvar() {
    let fotoUrl: string | undefined

    if (form.foto) {
      const fd = new FormData()
      fd.append('file', form.foto)

      const res = await fetch(
        `${process.env.NEXT_PUBLIC_API_URL}/entity/usuarios/upload-foto`,
        {
          method: 'POST',
          headers: {
            Authorization: `Bearer ${localStorage.getItem('entity_token')}`,
          },
          body: fd,
        },
      )

      const json = await res.json()
      fotoUrl = json.url
    }

    const payload: any = {
      nome: form.nome,
      email: form.email,
      perfil: form.perfil,
      ativo: form.ativo,
    }

    if (fotoUrl) payload.foto = fotoUrl

    if (editing) {
      await api.put(
        `/entity/usuarios/${editing.user_id}`,
        payload,
      )
    }

    setModalOpen(false)
    carregarUsuarios()
  }

  return (
    <CrudLayout
      title="Usu√°rios"
      subtitle="Gerencie os usu√°rios do sistema"
      actions={
        <div style={{ display: 'flex', gap: 10 }}>
          <button style={btnPdf}>
            <FileText size={16} /> PDF
          </button>
          <button style={btnExcel}>
            <FileSpreadsheet size={16} /> Excel
          </button>
          <button
            style={btnNovo}
            onClick={() => {
              setEditing(null)
              setModalOpen(true)
            }}
          >
            <Plus size={16} /> Novo usu√°rio
          </button>
        </div>
      }
    >
      <input
        placeholder="Buscar usu√°rio..."
        value={busca}
        onChange={e => {
          setBusca(e.target.value)
          setPage(1)
        }}
        style={search}
      />

      <CrudTable
        columns={[
          { key: 'nome', label: 'Nome' },
          { key: 'email', label: 'E-mail' },
          {
            key: 'ativo',
            label: 'Status',
            render: u => (
              <span
                style={{
                  padding: '4px 10px',
                  borderRadius: 12,
                  fontSize: 12,
                  background:
                    u.ativo === 'Sim'
                      ? '#e7f6ec'
                      : '#fdecea',
                  color:
                    u.ativo === 'Sim'
                      ? '#1e7e34'
                      : '#c62828',
                  fontWeight: 500,
                }}
              >
                {u.ativo === 'Sim' ? 'Ativo' : 'Inativo'}
              </span>
            ),
          },
          { key: 'actions', label: 'A√ß√µes' },
        ]}
        data={data}
        onEdit={abrirEditar}
      />

      <CrudPagination
        page={page}
        total={total}
        pageSize={pageSize}
        onPageChange={setPage}
      />

      {modalOpen && (
        <div style={overlay}>
          <div style={modal}>
            <h2>Editar usu√°rio</h2>

            <input
              placeholder="Nome"
              value={form.nome}
              onChange={e =>
                setForm({ ...form, nome: e.target.value })
              }
            />

            <input
              placeholder="E-mail"
              value={form.email}
              onChange={e =>
                setForm({ ...form, email: e.target.value })
              }
            />

            <select
              value={form.perfil}
              onChange={e =>
                setForm({
                  ...form,
                  perfil: e.target.value as any,
                })
              }
            >
              <option value="Usuario">Usu√°rio</option>
              <option value="Administrador">
                Administrador
              </option>
            </select>

            <select
              value={form.ativo}
              onChange={e =>
                setForm({
                  ...form,
                  ativo: e.target.value as any,
                })
              }
            >
              <option value="Sim">Ativo</option>
              <option value="N√£o">Inativo</option>
            </select>

            <input
              type="file"
              onChange={e =>
                setForm({
                  ...form,
                  foto: e.target.files?.[0] || null,
                })
              }
            />

            <div style={{ display: 'flex', gap: 10 }}>
              <button onClick={() => setModalOpen(false)}>
                Cancelar
              </button>
              <button onClick={salvar} style={btnNovo}>
                Salvar
              </button>
            </div>
          </div>
        </div>
      )}
    </CrudLayout>
  )
}

/* styles */

const search = {
  width: 280,
  padding: '8px 12px',
  marginBottom: 16,
  borderRadius: 6,
  border: '1px solid #d1d5db',
}

const baseBtn = {
  display: 'flex',
  alignItems: 'center',
  gap: 6,
  padding: '8px 14px',
  borderRadius: 6,
  border: 'none',
  cursor: 'pointer',
  color: '#fff',
}

const btnPdf = { ...baseBtn, background: '#0b1a3a' }
const btnExcel = { ...baseBtn, background: '#166534' }
const btnNovo = { ...baseBtn, background: '#16a34a' }

const overlay = {
  position: 'fixed' as const,
  inset: 0,
  background: 'rgba(0,0,0,0.4)',
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
}

const modal = {
  background: '#fff',
  padding: 24,
  borderRadius: 12,
  width: 420,
  display: 'flex',
  flexDirection: 'column' as const,
  gap: 12,
}
