'use client'

import { Eye, Pencil, Trash2 } from 'lucide-react'

type Column<T> = {
  key: keyof T | 'actions'
  label: string
  render?: (row: T) => React.ReactNode
}

type Props<T> = {
  columns: Column<T>[]
  data: T[]
  onEdit?: (row: T) => void
  onDelete?: (row: T) => void
  onView?: (row: T) => void
}

export default function CrudTable<T extends { user_id?: string }>(
  props: Props<T>,
) {
  const { columns, data, onEdit, onDelete, onView } = props

  return (
    <table style={{ width: '100%', borderCollapse: 'collapse' }}>
      <thead>
        <tr style={{ background: '#0b1a3a', color: '#fff' }}>
          {columns.map(col => (
            <th
              key={String(col.key)}
              style={{
                textAlign: 'left',
                padding: 14,
                fontSize: 14,
              }}
            >
              {col.label}
            </th>
          ))}
        </tr>
      </thead>

      <tbody>
        {data.map(row => (
          <tr key={(row as any).user_id}>
            {columns.map(col => (
              <td
                key={String(col.key)}
                style={{
                  padding: 14,
                  borderBottom: '1px solid #e5e7eb',
                  fontSize: 14,
                }}
              >
                {col.key === 'actions' ? (
                  <div
                    style={{
                      display: 'flex',
                      gap: 10,
                      justifyContent: 'flex-end',
                    }}
                  >
                    {onView && (
                      <Eye
                        size={18}
                        style={{ cursor: 'pointer' }}
                        onClick={() => onView(row)}
                      />
                    )}
                    {onEdit && (
                      <Pencil
                        size={18}
                        style={{ cursor: 'pointer' }}
                        onClick={() => onEdit(row)}
                      />
                    )}
                    {onDelete && (
                      <Trash2
                        size={18}
                        style={{ cursor: 'pointer', color: '#c62828' }}
                        onClick={() => onDelete(row)}
                      />
                    )}
                  </div>
                ) : col.render ? (
                  col.render(row)
                ) : (
                  String((row as any)[col.key])
                )}
              </td>
            ))}
          </tr>
        ))}
      </tbody>
    </table>
  )
}
